body {
  font-size: 18px;
  padding: 0px;
  margin: 0px;
}

.charater-sheet {
  padding: 10px 20px;
  border-left: solid 5px black;
}

.field-groups {
  /* display: flex; */
  /* flex-wrap: wrap; */
  /* max-width: 600px; */
  margin: 0px auto;
}

.field-group {
  /* flex: 50%; */
}

.field-group--title {
  text-transform: uppercase;
}

.input-group {
  padding: 0 0 5px;
}

.input-label {
  display: inline-block;
  width: 100px;
}

.input-field {
  border: 0px;
  border-bottom: solid 1px #ccc;
  background: white;
  padding: 3px;
  font-size: 20px
}

.input-group.is-expanded .input-label {
  width: 100%;
  display: block;
}

.input-group.is-expanded .input-field {
  width: 100%;
  height: 300px;
  border: solid 1px #ccc;
  padding: 20px
}